import { cilPeople, cilTrash } from '@coreui/icons';
import CIcon from '@coreui/icons-react';
import {
  CAvatar,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from '@coreui/react';
import axios from 'axios';
import moment from 'moment-jalaali';
import { lazy, useEffect, useState } from 'react';
import { api } from '../../api.js';
import Products from '../products/products/Products.js';

const WidgetsDropdown = lazy(() => import('../../widgets/WidgetsDropdown.js'));

const Dashboard = () => {
  const [users, setUsers] = useState([]);
  const [stores, setStores] = useState(0);
  const [products, setProducts] = useState(0);
  const userId = localStorage.userId;
  const role = localStorage.role;

  useEffect(async () => {
    const users = await axios.get(`${api}/api/users`);
    const stores = await axios.get(`${api}/api/stores`);

    if (role === 'admin') {
      const response = await axios.get(`${api}/api/products/${userId}`);
      setProducts(response?.data?.length);
    } else {
      const response = await axios.get(`${api}/api/products`);
      setProducts(response?.data?.length);
    }

    setUsers(
      users?.data?.map((el) => {
        return {
          avatar: {
            src: `${api}/uploads/customer/${el?._id}/${el?.avatar}`,
            avatar: el?.avatar,
          },
          user: {
            name: el?.firstName + ' ' + el?.lastName,
            new: false,
            registered: moment(el?.createdAt).format('jYYYY/jM/jD'),
          },
          country: {
            phoneNumber: el?.phoneNumber,
            countryCode: el?.countryCode,
            email: el?.email,
          },
          activity: moment(el?.updatedAt).format('jYYYY/jM/jD'),
          _id: el?._id,
        };
      })
    );

    setStores(stores?.data?.length);
  }, []);

  return (
    <>
      <WidgetsDropdown users={users} stores={stores} products={products} />

      {localStorage.role === 'super_admin' ? (
        <CRow>
          <CCol xs>
            <CCard className='mb-4'>
              <CCardHeader>لیست کاربران</CCardHeader>
              <CCardBody>
                <CTable align='middle' className='mb-0 border' hover responsive>
                  <CTableHead color='light'>
                    <CTableRow>
                      <CTableHeaderCell className='text-center'>
                        <CIcon icon={cilPeople} />
                      </CTableHeaderCell>
                      <CTableHeaderCell>کاربر</CTableHeaderCell>
                      <CTableHeaderCell>ایمیل</CTableHeaderCell>
                      <CTableHeaderCell>شماره همراه</CTableHeaderCell>
                      <CTableHeaderCell>فعالیت</CTableHeaderCell>
                      {role === 'super_admin' && (
                        <CTableHeaderCell>حذف</CTableHeaderCell>
                      )}
                    </CTableRow>
                  </CTableHead>
                  <CTableBody>
                    {users.map((item, index) => (
                      <CTableRow v-for='item in tableItems' key={index}>
                        <CTableDataCell className='text-center'>
                          {item?.avatar?.avatar && (
                            <CAvatar
                              size='md'
                              src={item.avatar.src}
                              status={item.avatar.status}
                            />
                          )}
                        </CTableDataCell>
                        <CTableDataCell>
                          <div>{item.user.name}</div>
                          <div className='small text-medium-emphasis'>
                            <span>{item.user.new ? 'کاربر جدید' : ''}</span> |
                            تاریخ عضویت: {item.user.registered}
                          </div>
                        </CTableDataCell>
                        <CTableDataCell>{item?.country?.email}</CTableDataCell>
                        <CTableDataCell>
                          {`${item?.country?.phoneNumber} ${item?.country?.countryCode}+`}
                        </CTableDataCell>
                        <CTableDataCell>
                          <div className='small text-medium-emphasis'>
                            آخرین بروزرسانی
                          </div>
                          <strong>{item.activity}</strong>
                        </CTableDataCell>
                        {role === 'super_admin' && (
                          <CTableDataCell>
                            <CIcon
                              icon={cilTrash}
                              className='text-danger'
                              size='lg'
                              onClick={() => {
                                console.log(item);
                                const confirm =
                                  window.confirm('آیا مطمئن هستید؟');
                                if (confirm) {
                                  axios
                                    .delete(`${api}/api/user/${item._id}`, {
                                      headers: {
                                        'x-auth-token': localStorage.token,
                                      },
                                    })
                                    .then((res) => {
                                      setUsers(
                                        users.filter(
                                          (el) => el._id !== item._id
                                        )
                                      );
                                    })
                                    .catch((err) => {
                                      console.log(err);
                                    });
                                }
                              }}
                            />
                          </CTableDataCell>
                        )}
                      </CTableRow>
                    ))}
                  </CTableBody>
                </CTable>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      ) : (
        <Products />
      )}
    </>
  );
};

export default Dashboard;
